import axios from 'axios'
import { Message } from 'element-ui'
import store from '../store'

axios.defaults.xsrfCookieName = 'csrftoken'
axios.defaults.xsrfHeaderName = 'X-CSRFToken'
// axios.defaults.withCredentials = true;   //session

// axios
const service = axios.create({
  baseURL: '', // apibase_url
  timeout: 5000                  // 
});

// request
service.interceptors.request.use(config => {
  // Do something before request is sent
    if (store.getters.token) {
        const token = store.getters.token;
        config.headers.Authorization = 'token ' + token; // token--['X-Token']key 
        config.headers['X-CSRFToken'] = token; //build post403
    }
    config.headers['Content-Type'] = "application/json;charset=utf-8";   //session
    return config;
}, error => {
  // Do something with request error
    console.log(error); // for debug
    Promise.reject(error);
});

// respone
service.interceptors.response.use(
  response => response,

  error => {
    console.log('err' + error);// for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    });
    return Promise.reject(error);
  }
);

export default service;



