import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 

const whiteList = ['/login']// 

router.beforeEach((to, from, next) => {
  NProgress.start() // Progress
  if (store.getters.islogin) { // token
    if (to.path === '/login') {
      next({ path: '/' })
      NProgress.done() // routerhash hash  afterEach hack pshistory
    } else {
        _checkToken().then(res => {
            const username = localStorage.getItem('username');
            store.dispatch('GenerateRoutes', { username }).then(() => { // 
                router.addRoutes(store.getters.addRouters); // 
                next({ ...to }) // hack addRoutes
            })
                next();
            }, function () {
                next({
                    path: '/login',
                    query: {redirect: to.fullPath}
                })
            })
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) { // 
      next()
    } else {
      next('/login') // 
      NProgress.done() // routerhash hash  afterEach hack pshistory
    }
  }
})

/**
 * TokenToken
 * */
function _checkToken() {
    return new Promise(function (resolve, reject) {
        const token = localStorage.getItem('token');
        const token_time = localStorage.getItem('token_time');
        const now_time = new Date().getTime();  // token 2
        if (token && (now_time - token_time) < 1000 * 60 * 60 * 2) {
            // token  https://segmentfault.com/q/1010000008595567/a-1020000008596744
            resolve();
        } else {
            localStorage.removeItem('token');
            localStorage.removeItem('token_time');
            reject();
        }
    })
}

router.afterEach(() => {
  NProgress.done() // Progress
})



